export function getMultilingualValue(valObj, localeId) {
    if (valObj && typeof valObj === 'object' && Object.keys(valObj).length > 0) {
        localeId = localeId || 'zh-CHS';
        const val = valObj[localeId];
        if (val !== undefined) {
            return val;
        } else {
            return '';
        }
    } else {
        return '';
    }
}