import { ChangeDetectorRef, Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild, ViewEncapsulation } from '@angular/core';

@Component({
    selector: 'new-scheme',
    templateUrl: './newscheme.component.html',
    encapsulation: ViewEncapsulation.None
})
export class NewSchemeComponent implements OnInit {

    @ViewChild('header') header: ElementRef<any>;

    @Input() dataPermissionIds = [];

    @Output() closeModal = new EventEmitter();
    @Output() submitModal = new EventEmitter();
    @Output() checkAuthority = new EventEmitter();

    schemeInfo = {
        name: '',
        isDefault: false,
        type: 'private',
        orgids: ''
    }

    schemeType = 'private';

    noPublicAuthority = false;
    noOrgAuthority = false;

    languages = [{ code: 'zh-CHS', name: '', default: true },{ code: 'en', name: 'English' },{ code: 'zh-CHT', name: ''}];
    nameMaxLength = { 'zh-CHS': 9, 'zh-CHT': 9, en: 20 };
    
    constructor(private cd: ChangeDetectorRef) { }

    ngOnInit(): void {
    }

    close($event) {
        this.closeModal.emit();
    }

    submit($event) {
        this.submitModal.emit(this.schemeInfo);
    }

    onCancelClick($event) {
        this.close($event);
    }

    onSelectedOrg($event) {
        if ($event.selections && $event.selections.length ) {
            let orgids = $event.selections.map(n => n.orgId);
            this.schemeInfo.orgids = orgids;
        }
    }

    onSetRangeType($event: MouseEvent, val) {
        $event.stopPropagation();
        this.schemeType = val;
        if (val === 'public' || val === 'org') {
            this.checkAuthority.emit({ value: val, schemeInfo: this.schemeInfo });
        } else {
            this.schemeInfo.type = val;
        }
    }

    refresh(hasAuthority = { public: true, org: true }) {
        this.schemeInfo = {...this.schemeInfo};
        this.schemeType = this.schemeInfo.type;
        this.noPublicAuthority = !hasAuthority.public;
        this.noOrgAuthority = !hasAuthority.org;
        this.cd.detectChanges();
    }

    getSelectedId($event) {
        if ($event.selections &&$event.selections.length) {
            this.schemeInfo.orgids = $event.selections.map(n => n.orgId);
        } else {
            this.schemeInfo.orgids = '';
        }
    }

    clearAllOrgs() {
        this.schemeInfo.orgids = '';
    }
}
