import { ChangeDetectorRef, Component, ElementRef, EventEmitter, Injector, Input, OnInit, Output, TemplateRef, ViewChild } from '@angular/core';
import { DatagridComponent } from '@farris/ui-datagrid';
import { LocaleService } from '@farris/ui-locale';
import { getMultilingualValue } from '../getValueWithLocaleId';

@Component({
    selector: 'scheme-list',
    templateUrl: 'scheme-list.component.html'
})

export class SchemeListComponent implements OnInit {

    @Input() items = [];
    
    @Output() closeModal = new EventEmitter();
    @Output() submitModal = new EventEmitter();

    @Input() removeItem = true;

    
    @ViewChild('header') header: ElementRef<any>;
    @ViewChild('schemeName') schemeName: TemplateRef<any>;
    @ViewChild('schemeIsDefault') schemeIsDefault: TemplateRef<any>;
    @ViewChild('btnDel') btnDel: TemplateRef<any>;
    @ViewChild('dg') dg: DatagridComponent;

    columns = [];

    currentUserId = '';
   
    private changes: {
        defaultId?: string,
        deletedIds?: any[]
    } = null;
    constructor(private cd: ChangeDetectorRef, private localeSer: LocaleService, private injector: Injector) { }

    ngOnInit(): void {

        const colName = this.localeSer.getValue('datagrid-scheme.columns.name') || '';
        const colType = this.localeSer.getValue('datagrid-scheme.columns.type') || '';
        const colDefault = this.localeSer.getValue('datagrid-scheme.columns.isDefault') || '';
        const colCreator = this.localeSer.getValue('datagrid-scheme.columns.username') || '';
        const colMgr = this.localeSer.getValue('datagrid-scheme.columns.mgr') || '';

        const publicTxt = this.localeSer.getValue('datagrid-scheme.isPublic') || '';
        const privateTxt = this.localeSer.getValue('datagrid-scheme.isPrivate') || '';
        const orgTxt = this.localeSer.getValue('datagrid-scheme.isOrg') || '';


        this.columns = [
            // {field: 'name', title: '', width: 150, template: this.schemeName},
            {field: 'name', title: colName, width: 150, formatter: (v) => {
                return getMultilingualValue(JSON.parse(v), this.localeSer.localeId);
            }},
            {field: 'type', title: colType, width: 50, halign: 'center', align: 'center', formatter: (v, d) => {
                switch(v) {
                    case 'public':
                        return '<span class="scheme-manager__label px-1 public">'+ publicTxt +'</span>';
                    case 'org':
                        return '<span class="scheme-manager__label px-1 org">'+ orgTxt +'</span>';
                    case 'private':
                    default:
                        return '<span class="scheme-manager__label px-1 private">'+ privateTxt +'</span>';
                }
            }},
            {field: 'isDefault', title: colDefault, width: 50, halign: 'center', align: 'center', template: this.schemeIsDefault },
            {field: 'userName', title: colCreator, width: 80},
            {title: colMgr, width: 50, template: this.btnDel, halign: 'center', align: 'center',}
        ];
    
    }

    onClose($event) {
        this.closeModal.emit();
    }

    onSubmitClick($event) {
        this.submitModal.emit( this.changes);
    }

    onCancelClick($event) {
        this.closeModal.emit();
    }

    setDefault($event: any, ctx) {
        const r = $event == '1';
        // $event.stopPropagation();
        this.items.forEach(n => {
            n.isDefault = false;
            if (n.id === ctx.rowData.id && r) {
                n.isDefault = r;
            }
        });

        this.items = [...this.items];

        const defaultItem = this.items.find(n => n.isDefault);
        if (defaultItem) {
            this.changes = this.changes || {};
            this.changes.defaultId = defaultItem.id;
        }
        // this.cd.detectChanges();
    }

    onSchemeDel($event, ctx) {
        if ((this.currentUserId === ctx.rowData["userId"] &&  ctx.rowData['type']=='private') || this.removeItem) {
            $event.stopPropagation();
            const id = ctx.rowData.id;
            this.items = this.items.filter(n => n.id !== id);
            this.changes = this.changes || {};
            this.changes.deletedIds = [...(this.changes.deletedIds || []), id];
            if (id === this.changes.defaultId) {
                this.changes.defaultId = '';
                delete this.changes.defaultId;
            }
        }
    }
}